import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  ExtraData,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, ConfigCofins } from '../../../../models/dtos/mlf/configCofins';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const ConfigCofinsSearchView: FC<SearchViewProps<ConfigCofins>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'codigoReceita',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código da Receita',
    },
    {
      dataField: 'periodoApuracao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Período de Apuração',
    },
    {
      dataField: 'diaVencimento',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Dia de Vencimento',
    },
    {
      dataField: 'aliquota',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Alíquota',
    },
    {
      dataField: 'recolhimentoMinimo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Recolhimento Mínimo',
    },
  ];

  const customExtraData: ExtraData = {
    hideRemoveButton: selectItem => selectItem.padrao,
  };

  return (
    <WindowComponent title="Configurações de CSLL">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, '', CLASS_NAME, customExtraData)}
      />
    </WindowComponent>
  );
};

export default ConfigCofinsSearchView;
